.footerBlock {
    height: 300px;
    background-color: #f409e7;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
.iconsBlock {
    display: flex;
    border: 1px solid;
    width: 80%;
    justify-content: space-evenly;

}
.icon {
    width: 50px;
    height: 50px;
    background-color: gray;
    flex-wrap: wrap;
}